<template>
  <section>
    <div class="loginMain " v-if="loginMain">
      <div class="loginTitle">ÁôªÈôÜ/Ê≥®ÂÜå</div>
      <ul>
        <li class="first">
          <input class="input" type="number" name="phone" v-model="phoneVal" maxlength="11"  @keyup.enter="getCodeF()" placeholder="ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑" />
          <button class="getCode" @click="getCodeF()" :class="{active:rightPhone,hide:beginCount || reSend || sending}">Ëé∑ÂèñÈ™åËØÅÁ†Å</button>
          <button class="getCode" v-show="sending" @click="getPopMin('sending')">ÂèëÈÄÅ‰∏≠...</button>
          <button class="getCode" v-show="beginCount" @click="getPopMin('counting')">Â∑≤ÂèëÈÄÅ({{timeLeng}}s)</button>
          <button class="getCode active" @click="getCodeF()" v-show="reSend">ÈáçÊñ∞Ëé∑Âèñ</button>
          <span class="clearTel clear" v-show="hasPhone" @click="clearPhoneF()">
            <img src="../../static/img/cha.jpg" alt="" />
          </span>
        </li>
        <li class="second">
          <input class="input" type="number" name="code" v-model="codeVal"  @keyup.enter="loginFun()"  placeholder="ËØ∑ËæìÂÖ•È™åËØÅÁ†Å" />
          <span class="clearCode clear" @click="clearCodeF()" v-show="hasCode">
            <img src="../../static/img/cha.jpg" alt="" />
          </span>
        </li>
        <li class="third">
          <div class="loginButton" :class="{active:rightPhone && rightCode}" @click="loginFun()" >
            {{ loginTxt }}
          </div>
        </li>
      </ul>
    </div>
    <popMsg :getCodeOK="getCodeOK" :out="out" :code="code"></popMsg>
    <popMin :popMin="popMin" :popOut="popOut" :popClass="popClass" :msg="msg"></popMin>
  </section>
</template>

<script>
import popMsg from './popMsg'
import popMin from './popMin.vue'

export default {
  name: 'login',
  data () {
    return {
      pageTitle: 'ÁôªÈôÜ/Ê≥®ÂÜå',
      loginTxt: 'ÁôªÂΩï',
      phoneVal: '',
      codeVal: '',
      hasPhone: false,
      rightPhone: false,
      hasCode: false,
      rightCode: false,
      isgetCode: false,
      timeLeng: 60,
      timeInt: null,
      reSend: false,
      beginCount: false,
      sending: false,
      code: '',
      getCodeOK: false,
      out: false,
      logined: false,
      popClass: 'icon-sign',
      popMin: false,
      popOut: false,
      msg: '',
      loginMain: true,
      flag: 1,
    }
  },
  components: {
    popMin,
    popMsg,
  },
  methods: { 
    // Ëé∑ÂèñÈ™åËØÅÁ†Å
    getCodeF: function() {
      if (this.rightPhone){
        this.sendfunc()  // ÂèëÈÄÅ‰∏≠


        // Áî®Âª∂ËøüÊ®°ÊãüajaxËØ∑Ê±Ç
        setTimeout(() => {
          if(this.phoneVal=='18852956186' || this.phoneVal=='18521796320'){
            this.timefunc()//ÂÄíËÆ°Êó∂
            this.isgetCode = true

            // Ê®°ÊãüÁü≠‰ø°ÂèëÈÄÅ
            this.createCode()
            this.getCodeOK = true//Â∑≤Ëé∑ÂèñÈ™åËØÅÁ†ÅÔºåÂèëÈÄÅ‰ø°ÊÅØ
            setTimeout(() => this.out = true,5000);//ÈöêËóèÂèëÈÄÅÈ™åËØÅÁ†ÅÊ†∑Âºè
            setTimeout(() => {
              this.getCodeOK = false
              this.out = false
            },6000);//ÊÅ¢Â§çÈ™åËØÅÁ†ÅÂàùÂßãÊ†∑Âºè

          } else {
            this.popMinShow("icon-sign","ÂèëÈÄÅÂ§±Ë¥•")
            // ÂèëÈÄÅÂ§±Ë¥•ÂêéÈáçÁΩÆ
            this.sending = false
            this.reSend = true
          }
        },1000);//ÈöêËóèÈ™åËØÅÁ†ÅÊ†∑Âºè


      } else if (!this.rightPhone){
        if(this.hasPhone){
          this.popMinShow("icon-sign","ËØ∑Â°´ÂÜôÊ≠£Á°ÆÁöÑÊâãÊú∫Âè∑")
        } else {
          this.popMinShow("icon-sign","ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑")
        }
      }
    },
    createCode: function() {
      let code = "";    
      let codeLength = 4;//È™åËØÅÁ†ÅÁöÑÈïøÂ∫¶
      for(let i = 0; i < codeLength; i++) {//Âæ™ÁéØÊìç‰Ωú
        code += Math.floor(Math.random()*10);//Ê†πÊçÆÁ¥¢ÂºïÂèñÂæóÈöèÊú∫Êï∞Âä†Âà∞code‰∏ä
      }
      this.code = code
    },
    clearPhoneF: function(){
      this.phoneVal = ''
    },
    clearCodeF: function() {
      this.codeVal = ''
    },
    countDown: function(){  //ÂÄíËÆ°Êó∂ÂáΩÊï∞
      console.log("ÂºÄÂßãÂÄíËÆ°Êó∂")
      if (this.rightPhone) {
        this.timeLeng--
        if (this.timeLeng <= 0) {
          this.clearCountF()
        }
      }
    },
    clearCountF: function(){
      if (this.timeInt) {
        clearInterval(this.timeInt)
        this.timeLeng = 60
        this.timeInt = null
        this.beginCount = false
        this.reSend = true
      }
    },
    sendfunc: function(){
      this.sending = true
      this.reSend = false
    },
    timefunc: function(){
      this.sending = false
      this.beginCount = true
      this.reSend = false
      this.timeInt = setInterval(this.countDown,1000)
    },
    getPopMin: function(str) {
      if ( str==='sending' ) {
        this.popMinShow("icon-sign","ÂèëÈÄÅ‰∏≠ÔºåËØ∑Á®çÂÄô")
      } else if ( str==='counting' ) {
        this.popMinShow("icon-sign","60sÂêéÈáçÊñ∞Ëé∑Âèñ")
      }
    },
    loginFun: function() {
      if(this.rightCode && this.rightCode && !this.logined){
        this.logined = true
        this.loginTxt = 'ÁôªÂΩï‰∏≠...'

        // Ê®°ÊãüÁôªÂΩïÊàêÂäü
        setTimeout(() => {
          this.loginTxt = 'ÁôªÂΩï'
          this.loginMain = false//ÁôªÂΩïÊàêÂäüÂêé
        },3000);

      }
    },
    popMinShow: function(icon, msg){
      if(this.flag){
        this.flag = 0
        this.popMin = true
        this.popClass = icon
        this.msg = msg
        setTimeout(() => this.popOut = true ,2000);
        setTimeout(() => {
          this.popOut = false//2sÂêéÊèêÁ§∫Ê∂àÂ§±
          this.popMin = false
          this.popClass = ''
          this.msg = ''
          this.flag = 1
        },3000);
      }
    }
  },
  watch:{
    phoneVal: function(){
      let phonTest = (/^1[34578]\d{9}$/.test(this.phoneVal));
      if (this.phoneVal.length > 0 && this.phoneVal.length < 12){
        this.hasPhone = true
      } else if (this.phoneVal.length > 11){
        this.phoneVal = this.phoneVal.slice(0,11)
        this.hasPhone = false
      } else {
        this.hasPhone = false
      }
      if (phonTest) {
        this.rightPhone = true
      } else {
        this.rightPhone = false
      }
    },
    codeVal: function() {
      if (this.codeVal.length > 0){
        this.hasCode = true
      } else {
        this.hasCode = false
      }
      if (this.codeVal.length == 4 && this.isgetCode) {
        this.rightCode = true
      } else {
        this.rightCode = false
      }
    }
  },
}
</script>



